<template>
  <v-app>
    <v-app-bar app color="white" elevation="2">
      <!-- <h2>以不同超商做為加盟店之綜合結果</h2> -->
      <router-link
        :to="{ path: 'main_page' }"
        :class="{ active: $route.path === '/main_page' }"
      >
        最終結果
      </router-link>
      &nbsp;&nbsp;|&nbsp;
      <router-link
        :to="{ path: 'other_page' }"
        :class="{ active: $route.path === '/other_page' }"
      >
        各個因素
      </router-link>
      <!-- &nbsp;&nbsp;|&nbsp;人口數&nbsp;&nbsp;|&nbsp;車流量&nbsp;&nbsp;|&nbsp;競爭數量&nbsp;&nbsp;|&nbsp;租金價格&nbsp;&nbsp;|&nbsp;所得水準 -->
    </v-app-bar>
    <v-main>
      <!-- 给應用提供合適的距離 -->
      <!-- <v-container fluid> -->
      <!-- 如果使用 vue-router -->
      <router-view></router-view>
      <!-- </v-container> -->
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: "App",
};
</script>

<style>
@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css);
@import url(https://fonts.googleapis.com/earlyaccess/notosanstc.css);

body {
  font-family: "Noto Sans TC", "Helvetica", "cwTeXYen", sans-serif, Arial,
    sans-serif;
  /* line-height: 2;
  font-weight: 400; */
  color: #24201e;
  /* background-color: #dfd8e0;
  margin: 0;
  padding: 0; */
}

#app {
  font-family: "Noto Sans TC", "Helvetica", "cwTeXYen", sans-serif, Arial,
    sans-serif;
  color: #24201e;
}

/* nav bar */
.v-app-bar a {
  color: #24201e;
  text-decoration: none;
  font-size: 16px;
}

.v-app-bar a.active {
  color: #618552;
  font-size: 20px;
}
/* cf6959 */
</style>



