import Vue from "vue";
import Router from "vue-router";
import HelloWorld from "@/components/HelloWorld";
import MainPage from "@/components/MainPage";
import OtherPage from "@/components/OtherPage";
// import seven_index from "@/components/maps/seven/seven_index";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/main_page",
      name: "MainPage",
      component: MainPage,
    },
    {
      path: "/other_page",
      name: "OtherPage",
      component: OtherPage,
    },
    {
      path: "/hello_world",
      name: "HelloWorld",
      component: HelloWorld,
    },
    // {
    //   path: "/seven_index",
    //   name: "seven_index",
    //   component: seven_index,
    // },
    // router 轉址
    { path: "/*", redirect: "/main_page" },
  ],
});



